<template>
  <div class="UserInfo">
    <div
      class="loading"
      v-if="isLoading"
    >
      <img src="../assets/img/loading.gif" />
    </div>
    <div
      class="userInfomation"
      v-else
    >
      <div class="authersummay">
        <div class="topbar">
          <span>作者</span>
        </div>
        <div class="maincontent">
          <img :src="userinfo.avatar_url" />
          <span class="user_name">{{userinfo.loginname}}</span>
          <div class="integral">
            <span>{{userinfo.score}}积分</span>
          </div>
          <div class="topic-collection">{{collectNumber}}个话题收藏</div>
          <div class="chaining">
            <span>Github：</span>
            <a :href="getgithub(userinfo.githubUsername)">@{{userinfo.githubUsername}}</a>
          </div>
          <div class="registration-time">注册时间 {{userinfo.create_at | formatDate}}</div>
        </div>

      </div>

      <!-- <section>
        <img :src="userinfo.avatar_url" />
        <span>{{userinfo.loginname}}</span>
        <p>{{userinfo.score}}积分</p>
        <p>注册时间：{{userinfo.create_at | formatDate}}</p>
      </section> -->
      <div class="replies">
        <p>最近参与的话题</p>
        <ul>
          <li v-for="item in userinfo.recent_replies">
            <router-link :to="{
                name:'post_content',
                params:{
                    id:item.id
                }
            }">{{item.title}}</router-link>
          </li>
        </ul>
      </div>
      <div class="topics">
        <p>最近创建的话题</p>
        <ul>
          <li v-for="item in userinfo.recent_topics">
            <router-link :to="{
                name:'post_content',
                params:{
                    id:item.id
                }
            }">{{item.title}}</router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UserInfo",
  data() {
    return {
      isLoading: false,
      userinfo: {},
      collectNumber: ""
    };
  },
  methods: {
    getgithub(github) {
      return "https://github.com/" + github;
    },
    getData() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
          this.isLoading = false;
          this.userinfo = res.data.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    getCollectData() {
      this.$http
        .get(
          `https://cnodejs.org/api/v1/topic_collect/${this.$route.params.name}`
        )
        .then(res => {
          this.collectNumber = res.data.data.length;
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.isLoading = true;
    this.getData();
    this.getCollectData();
  }
};
</script>

<style scoped>
.UserInfo {
  margin-right: 340px;
  margin-top: 15px;
  background-color: #e1e1e1;
}
.userInfomation {
  background: white;

  margin: 10px auto;
}
.userInfomation section {
  padding: 12px;
}
.userInfomation img {
  width: 30px;
}
.userInfomation li {
  list-style: none;
}
.userInfomation .replies,
.userInfomation .topics {
  font-size: 0.72rem;
  border-top: 10px #dddddd solid;
}
.userInfomation > div > p {
  padding: 12px 0 12px 12px;
  background-color: rgba(212, 205, 205, 0.17);
  font-size: 0.75rem;
  margin: 0;
}
.userInfomation > div > ul > li {
  padding: 4px 0 4px 12px;
  white-space: nowrap;
  font-size: 0.88rem;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 30px;
  vertical-align: middle;
}
.userInfomation > div > ul > li > a {
  color: #094e99;
  text-decoration: none;
}

.maincontent {
  padding: 0 10px 10px 0;
}
.maincontent img {
  width: 40px;
  border-radius: 3px;
  margin: 10px;
  vertical-align: middle;
}
.user_name {
  color: #778087;
}
.maincontent .integral {
  padding: 10px;
  font-size: 14px;
}
.maincontent .topic-collection {
  cursor: pointer;
  color: #778087;
  font-size: 14px;
  padding-left: 10px;
}
.maincontent .registration-time {
  color: #778087;
  font-size: 14px;
  padding: 10px;
}
.maincontent .chaining {
  padding: 10px;
  color: #778087;
  font-size: 14px;
}

.maincontent .chaining a {
  text-decoration: none;
  color: #778087;
  font-size: 14px;
}
</style>